import { Calculate } from "../../../../Images/@icons";
import { CalculateImage, DadImage } from "../../../../Images/@Image";
import { CardInfoGeneral, Content, DivImage, DivInfo, Title } from "./syle";

const CardInfoWhy = () => {
    return (
        <CardInfoGeneral>
            <Title>
                <h1>Por que Aagrispot?</h1>
                <hr></hr>
            </Title>
            <DivInfo>
            <DivImage>
            <img src={DadImage} />
            </DivImage>
            <Content>
                <p>
            {<b className="special_content">Aagrispot</b>}  um {<b className="special_content">anagrama</b>} (um jogo de palavras que utiliza a 
            transposio ou rearranjo de letras de uma palavra ou frase) do nome {<b className="special_content">Pitgoras</b>}, 
                um grande matemtico de sua poca e o criador de varias regras matemticas que utilizamos at os dias de {<b className="special_content">hoje</b>} .

                </p>
                <p>
                Ento por ser quem ele , resolvemos dar essa {<b className="special_content">homenagem</b>}.
                </p>
            </Content>
            
            
            </DivInfo>
            

        </CardInfoGeneral>);
}

export default CardInfoWhy